.line-v1 {
  margin: 20px 0;
  font-size: 16px;
  line-height: 1.2;
}
.line-v1__text {
  display: inline-block;
  min-width: 460px;
  padding: 14px 15px 10px 62px;
  position: relative;
  text-align: left;
}
.line-v1__text::before {
  content: "";
  width: 31px;
  height: 31px;
  position: absolute;
  top: calc(50% - 15.5px);
  left: 0;
  background-repeat: no-repeat;
  background-position: center right;
  opacity: 1;
  transition: 0.5s;
}
.line-v1__text--inst::before {
  background-image: url("../../img/icon-beat-inst2.svg");
}
.line-v1__text--vk::before {
  background-image: url("../../img/icon-beat-vk2.svg");
}
.line-v1__text--tlg::before {
  background-image: url("../../img/icon-beat-tlg2.svg");
}
.line-v1__text--dzen::before {
  background-image: url("../../img/icon-beat-yan2.svg");
}
.line-v1__text--yandex {
  padding-left: 108px;
}
.line-v1__text--yandex::before {
  background-image: url("../../img/icon-beat-yandex.svg");
  width: 79px;
  height: 30px;
  top: 10px;
}
.line-v1__link {
  background: rgba(44, 61, 82, 0.05);
  display: block;
  text-align: center;
  color: #000000;
  text-decoration: none;
  font-size: 16px;
  font-weight: 500;
  line-height: 22px;
}
.line-v1__link:hover .line-v1__text::before, .line-v1__link:focus .line-v1__text::before {
  opacity: 1;
}

@media (max-width: 767px) {
  .line-v1 {
    font-size: 13px;
    line-height: 1.2;
  }
  .line-v1__text {
    min-width: min-content;
    padding-right: 25px;
    padding-top: 4px;
    padding-bottom: 4px;
    padding-left: 76px;
    text-align: left;
  }
  .line-v1__text::before {
    left: 25px;
    width: 24px;
    height: 24px;
    background-size: 100%;
    top: calc(50% - 12px);
  }
  .line-v1__text--yandex {
    padding-left: 108px;
  }
  .line-v1__text--yandex::before {
    width: 64px;
    height: 24px;
    background-size: 100%;
    top: calc(50% - 12px);
    left: 25px;
  }
  .line-v1__text--vk::before {
    width: 31px;
    height: 17px;
    top: calc(50% - 8px);
  }
  .line-v1__link {
    font-size: 12px;
    line-height: 17px;
  }
}